import React from 'react'
import SearchForm from '../searchForm/SearchForm';
import RecipeInfoCard from '../recipe/RecipeInfoCard';

import './landing.css';

import axios from 'axios';

class Landing extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            latestRecipes: null
        }
    }
    componentDidMount(){
        axios.get('/recipes/latest', {headers: {'Content-Type': 'application/json'}})
        .then(recipes => {
            console.log('Latest Recipes: ', recipes);
            this.setState({latestRecipes: recipes});
        });
    }
    render(){
        let latestRecipes = this.state.latestRecipes.map(recipe => {
            return <RecipeInfoCard toggleModal={this.props.toggleModal} /> 
        })
        return (
            <div>
                <SearchForm toggleAddRecipeModal={this.props.toggleAddRecipeModal} />
                <div className="new-recipe-header">
                    Recently Added Recipes
                </div>
                <div className="container new-recipes">
                    
                    <div className="row">
                        {/* <div className="col s12 m4">
                            <RecipeInfoCard toggleModal={this.props.toggleModal} /> 
                        </div>
                        <div className="col s12 m4">
                            <RecipeInfoCard toggleModal={this.props.toggleModal} /> 
                        </div>
                        <div className="col s12 m4">
                            <RecipeInfoCard toggleModal={this.props.toggleModal} /> 
                        </div> */}
                        {latestRecipes}
                    </div>
                </div>

            </div>
        )
    }
}

export default Landing;